*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
/* navbar css */
nav.nav-conatiner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 16px;
  padding-bottom: 16px;
  background: #1e1c1c;
}
.logo {
  display: flex;
  align-items: center;
  margin-left: 20px;
  cursor: pointer;
}
img.logo-img {
  width: 60px;
  border-radius: 50%;
}
h3.logo-heading {
  color: whitesmoke;
  font-size: 30px;
  font-family: sans-serif;
  margin-left: 7px;
}
ul.nav-item-container {
  display: flex;
  margin-right: 20px;
  list-style: none;
}
a.nav-linking-item:hover {
  color: red;
}
a.nav-linking-item {
  text-decoration: none;
  color: whitesmoke;
  margin-left: 20px;
  font-size: 19px;
  font-family: sans-serif;
  transition: .4s all linear;
}